import css from "./Contact.module.css";

import icons from "../../icons/symbol-defs.svg";

const Contact = () => {
  return (
    <div className={`${css.contact} reveal fade-bottom`}>
      { // eslint-disable-next-line
      }<h6 id="Contact"></h6>
      <div className={css.contactContent}>
        <div className={css.contactLeft}>
          <h2>Kontakt</h2>
          <address className={css.address}>
            <h4>Modrzewiowe Domki</h4>
            <ul className={css.addressList}>
              <li className={css.addressListItem}>
                <h4 className={css.addressSubtitle}>
                  <svg className={css.icon}>
                    <use href={`${icons}#icon-envelop`}></use>
                  </svg>
                  Mail
                </h4>
                <a
                  className={css.contactLink}
                  href="mailto:modrzewiowe.domki@onet.pl"
                >
                  modrzewiowe.domki@onet.pl
                </a>
              </li>
              <li className={css.addressListItem}>
                <h4 className={css.addressSubtitle}>
                  <svg className={css.icon}>
                    <use href={`${icons}#icon-phone`}></use>
                  </svg>
                  Telefon
                </h4>
                <a className={css.contactLink} href="tel:+48694225226">
                  +48 694 225 226
                </a>
              </li>
              <li className={css.addressListItem}>
                <h4 className={css.addressSubtitle}>
                  <svg className={css.icon}>
                    <use href={`${icons}#icon-location`}></use>
                  </svg>
                  Lokalizacja
                </h4>
                ul. Wrzosowa 10, Sarbinowo, 76-034, woj. zachodniopomorskie
              </li>
            </ul>
          </address>
        </div>

        <iframe
          title="location"
          className={css.map}
          src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d2077.0642488971757!2d15.964714183084363!3d54.24334831016137!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4700302da9bdde1f%3A0x25e6e63a0f214f2b!2sModrzewiowe%20Domki!5e0!3m2!1spl!2spl!4v1683283826320!5m2!1spl!2spl"
          loading="lazy"
          referrerPolicy="no-referrer-when-downgrade"
        ></iframe>
      </div>
    </div>
  );
};

export default Contact;
